<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    return {
      textarea: ''
    }
  },
  methods: {
    clearCdn: function() {
      let reg1 = /^http:\/\//i,
          reg2 = /^https:\/\//i,
          falg = this.textarea.trim().match(reg1) || this.textarea.trim().match(reg2);

      if(this.textarea.trim() === '') {
        this.$Message.warning({
          content: '请输入需清除cdn的url',
          duration: 3
        })
      } else if (!falg) {
        this.$Message.warning({
          content: '请输入正确的url',
          duration: 3
        })
      } else {
        const token = getToken(),
              cdnUrl = this.textarea,
              params = {"token": token, "cdnUrl": cdnUrl};

        api.doClearCdnXin(params).then(res => {
          console.log(res)
          if(res.code == 0) {
            this.$Message.success({
              content: res.msg,
              duration: 3
            })
          } else {
            this.$Message.error({
              content: res.msg,
              duration: 5
            })
          }
        }).catch(err => {
          this.$Notice.error({
            desc: err,
          })
        })
      }
    }
  }
}
</script>
<template>
  <div class="textareaBox">
    <h3><Icon type="ios-nuclear-outline" size=24 />cdn目录缓存清理</h3>
    <div class="tips">
      <p class="tlt">要清理缓存的url集合，url的格式要求：</p>
      <p>1）URL 必须以'http://' 或 'https://' 开头，输入示例：http://www.a.com/image/test.png。</p>
      <p>2）每个url最大长度 2000 字符。</p>
      <p>3）刷新url每日不超过100条。</p>
      <p>4）每次填写的url总数不超过20条。</p>
    </div>
    <Input v-model="textarea" type="textarea" :autosize="{minRows: 10,maxRows: 16}" placeholder="在此填写需清除cdn的url..."></Input>
    <Button type="info" class="clearBtn" @click="clearCdn" ghost>清除</Button>
  </div>
</template>
<style lang="scss" scoped>
  .textareaBox {
    width: 40%;
    h3 {
      line-height: 40px;
      font-size: 18px;
      i {
        margin: -2px 4px 0 0;
      }
    }
    p {
      font-size: 14px;
      line-height: 40px;
    }
    .tlt {
      font-size: 15px;
    }
    .clearBtn {
      width: 100px;
      margin-top: 20px;
    }
  }
</style>